import React from 'react'
import './index.less'
import {Button, message, Modal, Spin, Switch} from 'antd';
import axios from "axios";
import {baseUrl} from "../../../../config";
import moment from 'moment'
import {UpdateSkill} from "./UpdateSkill";

export default class SkillsPosted extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            myFavs: [],
            idActive: 0,
            visibleCommunications: false,
        }
    }

    componentWillMount() {
        console.log(this.state);
        this.getFav()
    }

    getCommunications = (id) => {
        const ws = this.state.myFavs;
        for (const w in  ws) {
            const p = ws[w];
            console.log(p);
            if (p.id === id) {
                // console.log('avl', p);
                return (
                    <div>
                        <h2 className='name'>手机号:{p.communications.phone}</h2>
                        <img src={`${baseUrl}/${p.communications.wx_png}`} alt=''
                             className='avatar'
                             width='100%'
                        />
                        <h5>提示：长按识别二维码添加微信</h5>
                    </div>
                )
            }
        }
    };
    getFav = () => {
        // 有无缓存？返回：请求->缓存->返回
        // const cached = localStorage.works_by_me!==undefined;
        const cached = false;

        if (cached) {
            // 返回数据
            this.setState({
                myFavs: JSON.parse(localStorage.jinengs),
            })
        } else {
            // 请求
            axios.get(
                baseUrl + '/api/get_my_jinengs',
                {
                    params: {
                        token: localStorage.customer_token,
                    }
                })
                .then((r) => {
                    if (r.data.jinengs) {
                        const d = {
                            1: 172800,      //2*24*60*60      快递代取
                            2: 120,//          电费代充
                            3: 604800,//7*24*60*60   电影票代买
                            4: 120,//          跑腿
                            5: 4320,//          账号出租
                            6: 4320,//          课程服务
                            7: 4320,//          拍照
                            8: 4320,//          修图
                            9: 4320,//          化妆
                            10: 1440,//60*24     游戏陪玩
                            11: 4320,//          电车出租
                            12: 1440,//60*24     司机
                            13: 10080,//60*24*7   服务员
                            14: 4320,//          礼仪
                            15: 10080,//          模特
                            16: 4320,
                            17: 259200,//3*24*60*60
                            18: 604800,//7*24*60*60
                            19: 604800,//7*24*60*60
                            20: 432000,//5*24*60*60

                        };
                        let js = [];
                        const r_js = r.data.jinengs;
                        const r_fs = r.data.jineng_files;
                        const r_q = r.data.quantity;
                        r_js.map(j => {
                            r_fs.map(f => {
                                //图片
                                if (f.id_inf === j.id) {
                                    j.content = f.content
                                }
                            });
                            for (const q in r_q) {
                                //权值
                                const v = r_q[q];
                                // console.log(q,v,j,q==j.id)
                                if (q == j.id) {
                                    j.quantity = v;
                                }
                            }
                            //available
                            const now = moment().unix();
                            console.log();
                            if (d[j.jineng_tpl] + j.updated_at > now || j.quantity !== 1) {
                                j.avilable = false;
                            } else {
                                j.avilable = true;
                                console.log('jjj', j)
                            }
                            console.log('正在遍历j', j);
                            j.avilable = (parseInt(d[j.jineng_tpl]) + parseInt(j.updated_at) > now && j.status === 1);
                            js.push(j)
                        });
                        this.setState({
                            myFavs: js.reverse(),
                        });
                        // 设置缓存
                        localStorage.jinengs_by_me = JSON.stringify(r.data.jinengs)
                    }

                });
        }
    };
    showUpdateForm = (i) => {
        this.setState({
            visibleCommunications: true,
            modalContent: <UpdateSkill origin={i}/>
        });
    };
    handleOk = () => {
        this.setState({
            visibleCommunications: false,
        });
    };
    handleCancel = () => {
        this.setState({
            visibleCommunications: false,
        });
    };
    convertTpl = (id) => {
        const d = {
            1: '快递代取',
            2: '电费代充',
            3: '电影票代买',
            4: '跑腿',
            5: '账号出租',
            6: '课程服务',
            7: '拍照',
            8: '修图',
            9: '化妆',
            10: '游戏陪玩',
            11: '电车出租',
            12: '司机',
            13: '服务员',
            14: '礼仪',
            15: '模特',
            16: '配音',
            17: '打印',
            18: '住宿',
            19: '商家',
            20: '微信群',

        };
        return d[id]
    };
    hideSkill = (id, avilable, status, tpl) => {
        if (avilable) {
            // 下架
            axios.get(
                baseUrl + '/api/hide_jineng',
                {
                    params: {
                        token: localStorage.customer_token,
                        id_jineng: id,
                        status: avilable ? 2 : 1,
                        jineng_tpl: tpl,
                    }
                })
                .then((r) => {
                    console.log('返回数据', r);
                    if (r.data === 1) {
                        message.success('操作成功');

                    }
                    this.getFav()
                })
        } else {
            // 上架
            axios.get(
                baseUrl + '/api/hide_jineng',
                {
                    params: {
                        token: localStorage.customer_token,
                        id_jineng: id,
                        status: avilable ? 2 : 1,
                        jineng_tpl: tpl,
                    }
                })
                .then((r) => {
                    console.log('返回数据', r);
                    if (r.data === 1) {
                        message.success('操作成功');

                    }
                    this.getFav()
                })
        }

    };
    jumpToSkillIndex = (id, event) => {
        console.log(JSON.stringify(event.target.className.search('ant-switch')), JSON.stringify("update-skill"));
        if (JSON.stringify(event.target.className) === JSON.stringify('update-skill') || event.target.className.search('ant-switch') !== -1 || JSON.stringify(event.target.className) === JSON.stringify('skill-img')) {
            return 0;
        } else {
            window.location.hash = `#/skillIndex/${id}`

        }
    };

    renderFav = () => {
        if (this.state.myFavs.length === 0) {
            return <h2>暂无</h2>
        }
        let fs = [];
        const ws = this.state.myFavs;
        for (const w in  ws) {
            const i = ws[w];
            if (i.status !== 0) {

                fs.push(
                    <div className='cell-skill'>
                        <div
                            className='cell-skill'
                            onClick={this.jumpToSkillIndex.bind(this, i.id)}
                        >

                            <div className='cell-skill-main'>
                                <span className='bei_zhu'>{i.bei_zhu}</span>
                                <img
                                    className='skill-img'
                                    src={`${baseUrl}/${i.content}`}
                                    alt=''
                                    onClick={() => {
                                        const WeixinJSBridge = window.WeixinJSBridge;
                                        WeixinJSBridge.invoke("imagePreview", {
                                            "urls": [`${baseUrl}/${i.content}`],
                                            "current": `${baseUrl}/${i.content}`
                                        });
                                    }}
                                />
                                <span
                                    style={{float: "right"}}
                                >
                                下线：
                            <Switch
                                checked={!i.avilable}
                                onClick={this.hideSkill.bind(this, i.id, i.avilable, i.status, i.jineng_tpl)}
                            />
                            </span>
                            </div>
                            <div className='cell-skill-footer'>
                                <span className='tpl-tag'>{this.convertTpl(i.jineng_tpl)}</span>
                                <span
                                    onClick={this.showUpdateForm.bind(this, i)}
                                    className='update-skill'
                                >修改</span>
                                <span className='c-time'>上线时间:{moment.unix(i.created_at).format("MM-DD HH:mm")}</span>
                            </div>
                        </div>
                    </div>
                )
            }

        }

        return fs
    };

    render() {
        return (
            this.state.myFavs !== undefined
                ?
                <div className='skills-posed'>

                    <div className='cells-container-skills'>
                        {this.renderFav()}
                        <Modal
                            visible={this.state.visibleCommunications}
                            onOk={this.handleOk}
                            onCancel={this.handleCancel}
                            style={{textAlign: 'center'}}
                        >
                            {this.state.modalContent}
                        </Modal>
                    </div>
                </div>
                : <Spin/>

        )

    }
}
