import {configure, observable, computed, action, runInAction} from 'mobx';
import axios from 'axios'
import {baseUrl} from "../../../config";
import * as moment from "moment";
// configure({
//     enforceActions: 'observed'
// });

class Store {
    // 技能
    @observable skills = [];

    @observable works = [];
    @observable cmms = [];

    @observable showedOb = 0;

    @computed get workers() {
        return this.works.workers ? this.works.workers : [];

    }

    @computed get workInfo() {
        let r = this.works.work_info ? this.works.work_info[0] : [];
        if (this.works.work_url != undefined && this.works.work_url.length != 0) {
            console.log(this.works.work_url);
            r.work_url = decodeURI(this.works.work_url[this.works.work_info[0].id][0].content)
        }

        return r;
    }

    @computed get workEnroll() {
        return this.works.is_enroll;
    }

    @computed get communication() {
        return this.works.communication[0];
    }

    @computed get isSenter() {
        return this.works.is_senter;
    }

    @computed get showed() {
        return this.works.work_info ? this.works.work_info[0].showed : false;
    }

    @computed get workeFile() {
        return this.works.work_file ? this.works.work_file : [];
    }

    @action setSkills = (v) => {
        this.skills = v;
    };

    @action
    async fetchSkills() {
        await axios.get(
            baseUrl + '/api/get_jinengs',
            {
                params: {
                    token: localStorage.customer_token,
                }
            })
            .then((r) => {
                console.log("in axios ", r.data);
                const d = {
                    1: 172800,      //2*24*60*60      快递代取
                    2: 120,//          电费代充
                    3: 604800,//7*24*60*60   电影票代买
                    4: 120,//          跑腿
                    5: 4320,//          账号出租
                    6: 4320,//          课程服务
                    7: 4320,//          拍照
                    8: 4320,//          修图
                    9: 4320,//          化妆
                    10: 1440,//60*24     游戏陪玩
                    11: 4320,//          电车出租
                    12: 1440,//60*24     司机
                    13: 10080,//60*24*7   服务员
                    14: 4320,//          礼仪
                    15: 10080,//          模特
                    16: 4320,
                    17: 259200,//3*24*60*60
                    18: 604800,//7*24*60*60
                    19: 604800,//7*24*60*60
                    20: 432000,//5*24*60*60
                };
                let js = [];
                const now = moment().unix();

                const r_js = r.data.jinengs;
                const r_fs = r.data.jineng_files;
                const r_q = r.data.quantity;
                r_js.map(j => {
                    r_fs.map(f => {
                        //图片
                        if (f.id_inf === j.id) {
                            j.content = f.content
                        }
                    });

                    for (const q in r_q) {
                        //权值
                        const v = r_q[q];
                        const fuck_v = r_q[(0-q)]?parseInt(r_q[(0-q)]):0;
                        if (q == j.id) {
                            // console.log(q,v)
                            j.quantity = v+fuck_v;
                            // j.quantity = parseInt(d[j.jineng_tpl]) + parseInt(j.updated_at)+fuck_v;
                        }
                    }

                    //加入available
                    // console.log(d[j.jineng_tpl], j.updated_at, parseInt(d[j.jineng_tpl]) + parseInt(j.updated_at), now, parseInt(d[j.jineng_tpl]) + parseInt(j.updated_at) > now);
                    j.avilable = (parseInt(d[j.jineng_tpl]) + parseInt(j.updated_at) > now && j.status === 1);
                    j.expired = (parseInt(d[j.jineng_tpl]) + parseInt(j.updated_at) > now);
                    // console.log('正在遍历j', parseInt(d[j.jineng_tpl]) + parseInt(j.updated_at) > now, j.quantity === 1);
                    js.push(j)

                });
                // 1在前
                const one = js.filter((j) => j.avilable);
                // 比较权值大小
                one.sort((a,b)=>{
                    console.log(a.quantity,b.quantity)
                    if (a.quantity===undefined||b.quantity===undefined){
                        return -1;
                    }
                    if (a.quantity > b.quantity ) {           // 按某种排序标准进行比较, a 小于 b
                        return -1;
                    }
                    if (a.quantity < b.quantity ) {
                        return 1;
                    }
                    if ( a.quantity===b.quantity){
                        return b.id-a.id;
                    }
                    return 0;
                });
                console.table(one);

                // 2在后
                const two = js.filter((j) => !j.avilable);
                two.sort((a,b)=>{
                    console.log(a.quantity,b.quantity)
                    if (a.quantity===undefined||b.quantity===undefined){
                        return -1;
                    }
                    if (a.quantity > b.quantity ) {           // 按某种排序标准进行比较, a 小于 b
                        return -1;
                    }
                    if (a.quantity < b.quantity ) {
                        return 1;
                    }
                    if ( a.quantity===b.quantity){
                        return b.id-a.id;
                    }
                    return 0;
                });
                console.table(two);
                // 比较权值大小
                one.push(...two);
                //上下线处理
                this.setSkills(one);
            })
            .catch((err) => {
                console.log("in axios ", err)
            });
    }

}

export default new Store();