import {Upload, Icon, message, Form, Input, Modal, notification, Button} from 'antd';
import {baseUrl} from "../../../../config";
import React from "react";
import axios from "axios";
import moment from "moment";

const FormItem = Form.Item;


function getBase64(img, callback) {
    const reader = new FileReader();
    reader.addEventListener('load', () => callback(reader.result));
    reader.readAsDataURL(img);
}

function beforeUpload(file) {
    const isJpgOrPng = file.type === 'image/jpeg' || file.type === 'image/png';
    if (!isJpgOrPng) {
        message.error('You can only upload JPG/PNG file!');
    }
    const isLt2M = file.size / 1024 / 1024 < 2;
    if (!isLt2M) {
        message.error('Image must smaller than 2MB!');
    }
    return isJpgOrPng && isLt2M;
}


class WorkerCmmUploadForm extends React.Component {
    state = {
        loading: false,
        previewVisibleWechat: false,
        previewImageWechat: '',
        wechat: [],
    };

    handlePreviewWechat = async file => {
        if (!file.url && !file.preview) {
            file.preview = await getBase64(file.originFileObj);
        }
        this.setState({
            previewImageWechat: file.url || file.preview,
            previewVisibleWechat: true,
        });
        console.log('预览', this.state)
    };
    handleChangeWechat = ({fileList}) => {
        this.setState({wechat: fileList});
        console.log(this.state)
    };
    handleRemoveWechat = () => {
        axios.get(
            baseUrl + '/api/curd_communication',
            {
                params: {
                    'token': localStorage.customer_token,
                    // 'id_communication': this.state.communicationsId,
                    'phone': localStorage.phone,
                    'method': 'add',
                }
            })
            .then((r) => {
                console.log('r', r);
                this.setState({
                    communicationsId: r.data.Communication.id,
                    phoneValidate: r.data.Communication.phone,
                })
            });
        return true
    };
    handleChange = info => {
        if (info.file.status === 'uploading') {
            this.setState({loading: true});
            return;
        }
        if (info.file.status === 'done') {
            // Get this url from response in real world.
            getBase64(info.file.originFileObj, imageUrl =>
                this.setState({
                    imageUrl,
                    loading: false,
                }),
            );
        }
    };
    handleSubmit = () => {
        notification.open({
            message: '上传成功',
            description: '正在刷新...',
            placement: 'bottomLeft',
            icon: <Icon type="smile" style={{color: '#108ee9'}}/>,
        });
        setTimeout(() => {
            document.location.reload()
        }, 800);

    };
    insideSubmit = () => {
        console.log(this.state.wechat,this.state.wechat.length===0);
        if (this.state.wechat.length===0) {
            console.log('in');
            let allVals = this.props.form.getFieldsValue();
            console.log(allVals);
            this.props.form.validateFields((err, values) => {
                if (!err) {
                    axios.get(
                        baseUrl + '/api/curd_communication',
                        {
                            params: {
                                token: localStorage.customer_token,
                                method: 'add',     //给自己增加一组联系方式
                                phone: values.phone,
                            }
                        })
                        .then((r) => {
                            console.log(r.data);
                            const d = r.data;
                            if (d.success){
                                message.success('上传成功');
                                // Modal.destroyAll()
                            } else {
                                message.error(d.sorry || d.error);
                            }
                        })
                }
            })
        }else {
            message.success('已上传');
        }

    };
    componentWillUnmount() {
        console.log('lll')
    }

    render() {
        const uploadButton = (
            <div>
                <Icon type={this.state.loading ? 'loading' : 'plus'}/>
                <div className="ant-upload-text">Upload</div>
            </div>
        );
        const {getFieldDecorator, getFieldValue} = this.props.form;
        const {wechat, previewVisibleWechat, previewImageWechat} = this.state;
        return (
            <div>

                <Form>

                    <FormItem label='联系方式(微信或手机号)'>
                        {getFieldDecorator('phone', {
                            initialValue: '' || this.state.phoneValidate,
                            rules: [{
                                required: true,
                                message: '不能为空'
                            }]
                        })(<Input placeholder='18888888888'/>)
                        }
                    </FormItem>
                    <FormItem label='微信二维码'>
                        {getFieldDecorator('wx_png', {})(<div>
                            <Upload
                                className="wx-uploader"
                                name='wx_png'
                                action={`${baseUrl}/api/curd_communication?token=${localStorage.customer_token}&phone=${getFieldValue('phone')}`}
                                listType="picture-card"
                                fileList={wechat}
                                onPreview={this.handlePreviewWechat}
                                onChange={this.handleChangeWechat}
                                onRemove={this.handleRemoveWechat}
                                data={{
                                    token: localStorage.customer_token,
                                    method: 'add',     //给自己增加一组联系方式
                                    phone: getFieldValue('phone'),
                                }}
                            >
                                {wechat.length >= 1 ? null : uploadButton}
                            </Upload>
                            <p>请先填写好联系方式再点击上传二维码</p>
                            <Modal
                                visible={previewVisibleWechat}
                                footer={null}
                                onCancel={this.handleCancelWechat}
                                style={{width: '50%'}}
                            >
                                <img alt="example" style={{width: '100%'}} src={previewImageWechat}/>
                            </Modal>
                        </div>)
                        }
                    </FormItem>
                </Form>
                <Button
                    type='primary'
                    onClick={this.insideSubmit}
                >上传</Button>
            </div>
        );
    }
}

export const WorkerCmmUpload = Form.create()(WorkerCmmUploadForm);